.dish-btn-wrap {
    position: absolute;

    right: 4px;
    bottom: 4px;

    width: 28px;
    height: 28px;
}

.dish-btn-wrap div {
    position: relative;

    width: 28px;
    height: 28px;
}

.dish-btn-wrap svg {
    position: absolute;

    bottom: 0;
    right: 0;

    width: 20px;
    height: 20px;
    padding: 3px;

    color: var(--color-hard);
    border-radius: 6px;
    border: 1px solid #0002;
    background: var(--color-accent);

    transition: .25s all;
}


.dish-btn-wrap svg:nth-child(4).active {
    transform: rotateZ(180deg);
}

.dish-btn-wrap svg:nth-child(1).active{
    bottom: 60px;
}

.dish-btn-wrap svg:nth-child(2).active{
    bottom: 30px;
}

.dish-btn-wrap svg:nth-child(3).active{
    right: 30px;
}